$primary-blue: #215184;
$font-color: #545454;
$font-family: 'Brandon Grotesque', Arial, Helvetica, sans-serif;

$screen-md-min: 768px;
$screen-lg-min: 1440px;

@mixin md {
	@media (min-width: #{$screen-md-min}) {
		@content;
	}
}

@mixin lg {
	@media (min-width: #{$screen-lg-min}) {
		@content;
	}
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./font/BrandonGrotesque-Bold.woff2') format('woff2'),
		url('./font/BrandonGrotesque-Bold.woff') format('woff'),
		url('./font/BrandonGrotesque-Bold.ttf') format('truetype');
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./font/BrandonGrotesque-Regular.woff2') format('woff2'),
		url('./font/BrandonGrotesque-Regular.woff') format('woff'),
		url('./font/BrandonGrotesque-Regular.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./font/BrandonGrotesque-RegularItalic.woff2') format('woff2'),
		url('./font/BrandonGrotesque-RegularItalic.woff') format('woff'),
		url('./font/BrandonGrotesque-RegularItalic.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./font/BrandonGrotesque-Black.woff2') format('woff2'),
		url('./font/BrandonGrotesque-Black.woff') format('woff'),
		url('./font/BrandonGrotesque-Black.ttf') format('truetype');
	font-weight: 900;
	font-style: normal;
}

@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./font/BrandonGrotesque-Medium.woff2') format('woff2'),
		url('./font/BrandonGrotesque-Medium.woff') format('woff'),
		url('./font/BrandonGrotesque-Medium.ttf') format('truetype');
	font-weight: 500;
	font-style: normal;
}
